<template>
  <a
    class="twitter-timeline"
    href="https://twitter.com/MichelBillaud?ref_src=twsrc%5Etfw"
    >Tweets by MichelBillaud</a
  >
</template>
<script async src="https://platform.twitter.com/widgets.js" charset="utf-8"></script>
<script>
export default {
  name: "MichelBillaudTwitter",
  mounted() {
    const twitterJsScript = document.getElementById("twitterJsScript");
    if (twitterJsScript) {
      twitterJsScript.remove();
    }
    let script = document.createElement("script");
    script.setAttribute("src", "https://platform.twitter.com/widgets.js");
    script.setAttribute("charset", "utf-8");
    script.setAttribute("id", "twitterJsScript");
    document.body.appendChild(script);
  },
};
</script>

